import React from 'react';
import './App.css';
import Nav from "./components/Nav/Nav";
import {Route} from "react-router-dom";
import Settings from "./components/Settings/Settings";
import Music from "./components/Music/Music";
import News from "./components/News/News";
import ToDoContainer from "./components/ToDo/ToDoContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import Login from "./components/Login/Login";
import {connect} from "react-redux";
import Preloader from "./components/common/preloader/preloader";
import {initializeApp} from "./redux/appReducer";
import UsersContainer from "./components/Users/UsersContainer";
import SuspenseHOC from "./hoc/SuspenseHOC";

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));

class App extends React.Component {
    componentDidMount() {
        this.props.initializeApp()
    }

    render() {
        if (this.props.initialized === false) {
            return <Preloader/>
        }
        return (
            <div className='app-wrapper'>
                <HeaderContainer/>
                <Nav/>
                <div className='app-wrapper__content'>
                    <Route path='/dialogs'
                           render={ SuspenseHOC(DialogsContainer) }/>
                    <Route path='/profile/:userId?'
                           render={ SuspenseHOC(ProfileContainer) }/>
                    <Route path='/users' render={ SuspenseHOC(UsersContainer) }/>
                    <Route path='/news' render={() => <News/>}/>
                    <Route path='/music' render={() => <Music/>}/>
                    <Route path='/settings' render={() => <Settings/>}/>
                    <Route path='/ToDo' render={ SuspenseHOC(ToDoContainer) }/>
                    <Route path='/Login' render={() => <Login/>}/>
                </div>
            </div>

        );


    }
}

const mapStateToProps = (state) => {
    return {
        initialized: state.app.initialized
    }
}

export default connect(mapStateToProps, {initializeApp})(App)


