import React, {useState} from 'react';
import cn from "classnames"
import s from './paginator.module.css'

let Paginator = (props, {portionSize = 20}) => {
    let pageNumber = Math.ceil(props.totalUsersCount / props.pageSize);
    let page = [];
    for (let i = 1; i <= pageNumber; i++) {
        page.push(i)
    }
    let portionCount = Math.ceil(pageNumber/portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;


    return <div className={cn(s.paginator)}>
        { portionNumber > 1 &&
        <button className={s.button} onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button> }

        {page
            .filter(p => p >= leftPortionPageNumber && p<=rightPortionPageNumber)
            .map((p) => {
                return <span className={ cn({
                    [s.selectedPage]: props.currentPage === p
                }, s.pageNumber) }
                             key={p}
                             onClick={(e) => {
                                 props.onPageChanged(p);
                             }}>{p}</span>
            })}
        { portionCount > portionNumber &&
        <button className={s.button} onClick={() => { setPortionNumber(portionNumber + 1) }}>NEXT</button> }


    </div>
}

export default Paginator

